.galary {
   width: 100%;
   height: 100%;
   padding: 40px 0 40px 0;
}

.galary__wrapper {
   width: 100%;
   min-height: 100vh;
   display: flex;
   align-items: flex-start;
   justify-content: space-between;
}
.galary__menu {
   padding-right: 20px;
   display: flex;
   justify-content: center;
   align-items: center;
   border: 1px solid rgba(0, 0, 0, 0.3);
   border-radius: 20px;
   height: 500px;
   background: #E2EAF3;
   position: relative;
}
.galary__menu__items {
   width: max-content;
   list-style: none;
   margin-top: 60px;
   transition: all 0.3s;
}



@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700');


.galary__items {
    font-family: 'Roboto', sans-serif;
    display: grid;
    grid-template-columns: 380px 380px ;
    column-gap: 40px;
    row-gap: 40px;
}
a
{
    text-decoration: none;
}
.product-card {
    width: 380px;
    position: relative;
    box-shadow: 0 2px 7px #dfdfdf;
    
    background: #fafafa;
}

.badge {
    position: absolute;
    left: 0;
    top: 20px;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 700;
    background: red;
    color: #fff;
    padding: 3px 10px;
}

.product-tumb {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 300px;
    padding: 50px;
    background: #f0f0f0;
}

.product-tumb img {
    max-width: 100%;
    max-height: 100%;
}

.product-details {
    padding: 30px;
}

.product-catagory {
    display: block;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    color: #ccc;
    margin-bottom: 18px;
}

.product-details h4 a {
    font-weight: 500;
    display: block;
    margin-bottom: 18px;
    text-transform: uppercase;
    color: #363636;
    text-decoration: none;
    transition: 0.3s;
}

.product-details h4 a:hover {
    color: #fbb72c;
}

.product-details p {
    font-size: 15px;
    line-height: 22px;
    margin-bottom: 18px;
    color: #999;
}

.product-bottom-details {
    overflow: hidden;
    border-top: 1px solid #eee;
    padding-top: 20px;
}

.product-bottom-details div {
    float: left;
    width: 50%;
}

.product-price {
    font-size: 18px;
    color: #fbb72c;
    font-weight: 600;
}

.product-price small {
    font-size: 80%;
    font-weight: 400;
    text-decoration: line-through;
    display: inline-block;
    margin-right: 5px;
}

.product-links {
    text-align: right;
}

.product-links a {
    display: inline-block;
    margin-left: 5px;
    color: #e1e1e1;
    transition: 0.3s;
    font-size: 17px;
}

.product-links a:hover {
    color: #fbb72c;
}


.menu__Img {
    width: 150px;
    height: 60px;
    position: absolute;
    top: 20px;
    left: 40px;
    
}
.menu__Img img {
    width: 100%;
    height: 100%;
    object-fit: fill;
}


.linkDiv {
    background: green;
    width: max-content;
    border-radius: 15px;
    padding: 15px;
    margin-top: 20px;
    transition: all 0.3s;
    cursor: pointer;
}

.linkDiv:hover {
    transform: scale(0.78);
    transition: all 0.3s;
}
.PrevLink {
    text-decoration: none;
    color: white
}

.PrevLink:hover {
    color: white
}